.news {
    padding-left: 10px;
    border: 4px double black; /* Параметры границы */
    background: #fff; /* Цвет фона */
    padding-top: 10px; /* Поля вокруг текста */
    padding-right: 10px;
}
.buttonUpdateS {
}

.buttonUpdateS {
    box-sizing: border-box;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-color: black;
    border: 2px solid #e74c3c;

    color: #e74c3c;
    cursor: pointer;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-align-self: center;
    -ms-flex-item-align: center;
    align-self: center;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1;
    padding: 1.2em 2.8em;
    text-decoration: none;
    text-align: center;
    text-transform: uppercase;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
}

.buttonUpdateS:hover, .buttonUpdate:focus {
    color: #ffffff;
    outline: 0;
}

.buttonUpdateS {

    border-color: #db3434;
    color: #fff;
    box-shadow: 0 0 40px 40px #db34b4 inset, 0 0 0 0 #d034db;
    -webkit-transition: all 150ms ease-in-out;
    transition: all 150ms ease-in-out;
}

.buttonUpdateS:hover {
    box-shadow: 0 0 10px 0 #db3434 inset, 0 0 10px 4px #db3434;
}
